          <!-- <div class="discuss ">
             <div class="flex-space-between">
               <div class="h1">讨论区 （{{total}}条信息）</div>

               <div class="FLEX administration-box">
                 <div class="edit" @click="dialogFormVisible2 = true" v-show="subjectInfor.role == 'owner'">+添加人员</div>

                 &lt;!&ndash; <el-popover style="margin-left: 10px"
                    placement="bottom"
                    width="400"
                    trigger="click">
                    1111111
                    <el-button slot="reference" type="text" style="font-size: 12px;">管理</el-button>
                  </el-popover>&ndash;&gt;
               </div>

             </div>
             <div class="time more" v-show="comments.length > 0" @click="gomore">查看更多消息</div>
             <comment v-if="comments.length > 0" :comments="comments" :role="role"></comment>
             <div class="nocomment" v-else>
               <tip txt="暂无评论" oder="4"></tip>
             </div>


           </div>

           <div class="comment-all paddin50px FLEX"
                v-show="subjectInfor.role == 'member' || subjectInfor.role == 'owner' ">
             <i class="el-icon-chat-dot-round" style="font-size: 32px; line-height: 40px;color: #aaa;cursor: pointer"
                @click="dialogFormVisible = true">

             </i>

             <div class="comment-iput">
               <el-input v-model="commentinput" placeholder="请输入内容enter键添加评论"
                         @keyup.enter.native="commentadd"></el-input>
             </div>
             <div class="comment-btn" @click="commentadd">

               <el-button type="primary">发送</el-button>
             </div>
          </div>-->
        </el-card>
        <el-card shadow="hover" v-show="articlemines.length > 0">
          <div class="more" @click="gomore1">{{moreTxt}}</div>
        </el-card>
      </el-col>
      <el-col :span="6">
