<script>
import { mapGetters } from "vuex";
import Swiper from "swiper";
import comment from "@/components/Comment/comment";
import Avatar from "@/components/Avatar/index";
import BreadcrumbWrapper from "@/layout/BreadcrumbWrapper";
import Tip from "@/components/Tip/index";
import {
  materiallist,
  articlemine,
  commentlist,
  commentadd,
  subjectget,
  subjectclose,
  subjectopen,
  subjectmodify,
  infofind,
  subjectaddmember,
  commentfind,
  subjectremovemember,
  articlelist
} from "@/api/index";

export default {
  name: "Studio",
  components: {
    comment,
    BreadcrumbWrapper,
    Avatar,
    Tip
  },
  computed: {
    ...mapGetters(["name"]),
    isShowHintHideHint() {
      if (this.ischinese) {
        return true;
      } else {
        return false;
      }
      // ischinese ? subjectInfor.content.length >　165 : (subjectInfor.content.length)/2 >　165
    }
  },

  data() {
    return {
      moreTxt: "点击加载更多",
      value: true,
      person: '<div class="person"><img src="###" alt=""></div>',
      comments: [],
      id: this.$route.query.id,
      fromPage: this.$route.query.fromPage,
      uid: this.$store.state.user.user.id,
      materiallists: [],
      articlemines: [],
      commentinput: "",
      limit: 10,
      limit1: 100,
      page: 1,
      page1: 1,
      page2: 1,
      page3: 1,
      subjectInfor: {
        creator: {},
        members: []
      },
      total: 0,
      dialogFormVisible: false,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      dialogVisible3: false,
      tiptxt: "关闭开关后不能显示在大厅 ， 确认操作？",
      hide_flag: 0,
      formLabelWidth: "120px",
      key: "",
      key1: "",
      searchuser: [],
      commentfindlist: [],
      noned: "",
      moreT: "",
      flag: true,
      flag1: true,
      role: "",
      radio: "",
      isbounceInRight: false,
      slidefase: false,
      activeName: "first",
      txtmore: false,
      ischinese: false
    };
  },
  created() {
    // this.comments = comments.data;
  },
  mounted() {
    this.subjectget();
    /* this.commentlist()*/
    this.materiallist();
    /* this.articlemine()*/
    this.articlelist();
  },
  methods: {
    gomore1() {
      console.log("this.id 0= ", this.id);
      console.log("this.id 1= ", this.$route.query.id);
      if (this.flag) {
        if (this.key) {
          this.articlefind();
        } else {
          this.articlelist();
        }
      }
    },
    articlefind() {
      articlelist({
        sid: this.id,
        uid: this.activeName == "first" ? "" : this.uid,
        limit: this.limit,
        page: this.page
      }).then(res => {
        this.articlemines = [];
        this.articlemines = this.articlemines.concat(res.data.list);
        this.total = res.data.total;
        if (!(res.data.list.length > 0)) {
          this.isShow = true;
        }
        if (res.data.list.length < this.limit) {
          this.flag = false;
          this.moreTxt = "已无更多";
        } else {
          this.moreTxt = "点击加载更多";
          this.flag = true;
          this.page = ++this.page;
        }
      });
    },
    articlelist() {
      console.log("this.page", this.page);
      articlelist({
        sid: this.id,
        uid: this.activeName == "first" ? "" : this.uid,
        limit: this.limit,
        page: this.page
      }).then(res => {
        this.articlemines = this.articlemines.concat(res.data.list);
        this.total = res.data.total;
        if (res.data.list.length < this.limit) {
          this.flag = false;
          this.moreTxt = "已无更多";
        } else {
          this.moreTxt = "点击加载更多";
          this.flag = true;
          this.page = ++this.page;
        }
      });
    },
    isChinese(temp) {
      if (/[\u4e00-\u9fa5]/.test(temp)) {
        return true;
      }
      return false;
    },
    clicktxtmore() {
      this.txtmore = !this.txtmore;
    },
    onSubmit() {
      return false;
    },
    scrollToBottom() {
      this.$nextTick(() => {
        var container = this.$el.querySelector(".container");
        container.scrollTop = container.scrollHeight;
      });
    },
    subjectget() {
      subjectget({
        id: this.id
      }).then(res => {
        this.subjectInfor = res.data;
        this.role = res.data.role;
        this.hide_flag = res.data.hide_flag;
        this.ischinese = this.isChinese(res.data.content);

        /* debugger*/
        this.$nextTick(() => {
          var swiper = new Swiper(".swiper-container", {
            slidesPerView: 7,
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev"
            },
            hiddenClass: "btn-hidden" //某些情况下需要隐藏前进后退按钮时，可以设定一个自定义的类名。
          });
        });
      });
    },
    materiallist() {
      materiallist({
        sid: this.id,
        page: this.page3,
        limit: this.limit
      }).then(res => {
        this.materiallists = res.data.list;
      });
    },
    articlemine() {
      articlemine({
        sid: this.id,
        page: this.page3,
        limit: this.limit
      }).then(res => {
        this.articlemines = res.data.list;
      });
    },
    /* commentlist() {
         commentlist({
           sid: this.id,
           limit: this.limit,
           page: this.page

         }).then((res) => {
           // console.log('res' , res.data.list.reverse())
           this.total = res.data.total

           const {list} = res.data;

           const mergeComments = [...list.reverse(), ...this.comments]
           this.comments = mergeComments;
           if (res.data.list.length < this.limit) {
             this.flag = false

           } else {
             this.flag = true
           }
           this.page = ++this.page


         })

       },*/
    commentadd() {
      commentadd({
        sid: this.id,
        content: this.commentinput
      }).then(res => {
        this.commentinput = "";

        this.comments.push(res.data);
        this.scrollToBottom();
      });
    },
    changekey() {
      this.page1 = 1;
      this.commentfindlist = [];
    },
    commentfind() {
      if (this.key1 == "") {
        this.moreT = "";
        this.commentfindlist = [];

        return false;
      } else {
        commentfind({
          key: this.key1,
          sid: this.id,
          limit: this.limit,
          page: this.page1
        }).then(res => {
          if (res.data.list && res.data.list.length == 0) {
            this.noned = "无对应评论";
          } else {
            this.moreT = "查看更多";
          }
          this.commentfindlist = this.commentfindlist.concat(res.data.list);

          if (res.data.list && res.data.list.length < this.limit) {
            this.flag1 = false;
          } else {
            this.flag1 = true;
          }
          this.page1 = ++this.page1;
        });
      }
    },
    gomore() {
      if (this.key1 == "") {
        if (this.flag) {
          this.commentlist();
        }
      } else {
        if (this.flag1) {
          this.commentfind();
        }
      }
    },
    switchsubject(value) {
      if (this.subjectInfor.role == "owner") {
        if (value == "0") {
          this.dialogVisible3 = true;
          /* this.subjectclose()*/
          this.tiptxt = "切换后将不显示在大厅 ， 确认操作？";
        } else if (value == "1") {
          this.dialogVisible3 = true;
          this.tiptxt = "切换后将显示在大厅 ， 确认操作？";
        }
      } else {
        this.$message({
          message: "您无权限！",
          type: "error"
        });
      }
    },
    subjectclose() {
      subjectclose({
        id: this.id
      }).then(res => {
        this.subjectget();
      });
    },
    subjectopen() {
      subjectopen({
        id: this.id
      }).then(res => {
        this.subjectget();
      });
    },
    hide_flagConfir() {
      if (this.hide_flag == "0") {
        this.subjectclose();
        this.dialogVisible3 = false;
      }
      if (this.hide_flag == "1") {
        this.subjectopen();
        this.dialogVisible3 = false;
      }
    },
    subjectmodify(value) {
      console.log("value", value);
      this.dialogFormVisible1 = false;
      subjectmodify({
        id: this.id,

        title: value.title,
        content: value.content
      }).then(res => {
        if (res.code == 200) {
          this.$message({
            message: "修改成功！",
            type: "success"
          });
        }
      });
    },
    infofind() {
      infofind({
        key: this.key,
        limit: this.limit1,
        page: this.page2
      }).then(res => {
        this.searchuser = res.data.list;
        console.log("searchuser", this.searchuser);
      });
    },
    subjectaddmember(value) {
      if (value == this.uid) {
        this.$message({
          message: "不能添加 自己！",
          type: "error"
        });
      } else {
        subjectaddmember({
          id: this.id,
          uid: value
        }).then(res => {
          if (res.code == 200) {
            this.$message({
              message: "添加成员成功！",
              type: "success"
            });
          } else {
            this.$message({
              message: "添加成员失败！",
              type: "error"
            });
          }
          this.dialogFormVisible2 = false;
          this.subjectget();
        });
      }
    },
    getRadio(value) {
      this.isbounceInRight = !value;
    },
    subjectremovemember(value) {
      if (value == this.uid) {
        this.$message({
          message: "不能移除自己！",
          type: "error"
        });
      } else {
        subjectremovemember({
          id: this.id,
          uid: value
        })
          .then(res => {
            if (res.code == 200) {
              this.$message({
                message: "移除成功！",
                type: "success"
              });
              this.subjectget();
            }
          })
          .catch(error => {});
      }
    },
    addnumwitch() {
      this.slidefase = true;
    },
    goback() {
      this.slidefase = false;
    },
    handleClick(tab, event) {
      console.log("xxxxxxxxxxxxxxxxxxxx", tab);
      this.page = 1;
      this.articlemines = [];
      this.articlefind();
      console.log(tab, event);
    }
    // handleCurrentChange(val) {
    //   this.page = val
    //   if (this.activeName == 'first') {
    //     this.articlelist()
    //   }
    // }
  }
};
</script>

