
<!--评论模块-->
<template>
    <div class="container">
        <div :class="item.creator.id == id ? 'comment-right': 'comment'" v-for="item  in (comments)" :key="item.id">
          <div>
            <div class="info">
              <div @click="getuser(item)">
                <avatar :bgColor='item.creator.user_backcolor' style="width: 40px; height: 40px; line-height: 40px;" :name='item.creator.user_abbr' size="mini" ></avatar>
              </div>
              <div class="right" style="margin-right: 10px;">
                <div class="name">{{item.creator.user_name}}</div>
                <div class="date time">{{item.ptime}}</div>
              </div>
            </div>
            <div class="content" style="padding: 10px 0px">{{item.content}}</div>
          </div>
        </div>
      <el-dialog
        title=""
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <span>
          <div class="info FLEX">
            <avatar :bgColor='user.user_backcolor' :name='user.user_abbr' size="small" ></avatar>
                <div class="right" style="margin-left: 10px; line-height: 24px;">
                    <div class="name">{{user.user_name}}</div>
                    <div class="date">{{user.ptime}}</div>
                </div>
            </div>
        </span>
        <span slot="footer" class="dialog-footer">
    <el-button @click="subjectremovemember">移除</el-button>
    <el-button type="primary" @click="subjectbanmember">禁言</el-button>
  </span>
      </el-dialog>
    </div>
</template>

<script>
  import Vue from 'vue'

  import Avatar from '@/components/Avatar/index'
  import {subjectremovemember,subjectbanmember} from '@/api/index'
  export default {
    name: 'comment',
    props: {
      comments: {
        type: Array,
        default:[]
      },
      role: String
    },
    components: {
      Avatar
    },
    data() {
      return {
        inputComment: '',
        showItemId: '',
        id:this.$store.state.user.user.id,
        crruid:'',
        centerDialogVisible:false,
        user:{},
        sid:'',
      }
    },
    mounted(){
    },
    computed: {},
    methods: {
      subjectremovemember() {
          subjectremovemember({
            id: this.sid,
            uid: this.crruid
          }).then((res) => {
            if(res.code == 200){
              this.$message({
                message: '移除成功！',
                type: 'success'
              });

            }
            this.centerDialogVisible = false
          }).catch(error => {
            this.centerDialogVisible = false
          })
      },
      subjectbanmember() {
        subjectbanmember({
          id: this.sid,
          uid: this.crruid
        }).then((res) => {
          this.centerDialogVisible = false
          if(res.code == 200){
            this.$message({
              message: '禁言成功！',
              type: 'success'
            });
          }
        }).catch(error => {
          this.centerDialogVisible = false
        })
      },

      /**
       * 点赞
       */
      getuser(value){
        if(this.role == 'owner'){
          if(value.creator.id == this.id){
            this.$message({
              message: '您不能在该话题中禁言自己或者移除自己！',
              type: 'error'
            });
          }else {
            console.log('user' , value)
            this.centerDialogVisible = true
            this.user = value.creator

            this.sid = value.subject_id
            this.crruid = value.creator.id
          }
        }
        else {
          this.$message({
            message: '您无权限！',
            type: 'error'
          });

        }






      },
      likeClick(item) {
        if (item.isLike === null) {
          Vue.$set(item, 'isLike', true)
          item.likeNum++
        } else {
          if (item.isLike) {
            item.likeNum--
          } else {
            item.likeNum++
          }
          item.isLike = !item.isLike
        }
      },

      /**
       * 点击取消按钮
       */
      cancel() {
        this.showItemId = ''
      },

      /**
       * 提交评论
       */
      commitComment() {
        console.log(this.inputComment)
      },

      /**
       * 点击评论按钮显示输入框
       * item: 当前大评论
       * reply: 当前回复的评论
       */
      showCommentInput(item, reply) {
        if (reply) {
          this.inputComment = '@' + reply.fromName + ' '
        } else {
          this.inputComment = ''
        }
        this.showItemId = item.id
      }
    },
    created() {
     /* alert(this.comments.id)*/



    }
  }
</script>

<style scoped lang="stylus">

    .container {
        height 507px;
        padding: 0 0px;
        box-sizing: border-box;
        overflow-y scroll;
        .comment {
            display: flex;
            flex-direction: column;
            padding: 10px;
            border-bottom: 1px solid #F2F6FC;
            .info {
                display: flex;
                align-items: center;
                .avatar {
                    width:40px;
                    height:40px;
                    border-radius: 50%;
                }
                .right {
                    display: flex;
                    flex-direction: column;
                    margin-left: 10px;
                    margin-right 10px;
                    .name {
                        font-size: 16px;
                        color: #303133;
                        margin-bottom: 5px;
                        font-weight: 500;
                    }
                    .date {
                        font-size: 12px;
                        color: #909399;
                    }
                }
            }
            .content {
                font-size: 16px;
                color: #303133;
                line-height: 20px;
                padding: 10px 0;
            }
            .control {
                display: flex;
                align-items: center;
                font-size: 14px;
                color: #909399; //次要文字
                .like {
                    display: flex;
                    align-items: center;
                    margin-right: 20px;
                    cursor: pointer;
                    &.active, &:hover {
                        color: #409EFF;
                    }
                    .iconfont {
                        font-size: 14px;
                        margin-right: 5px;
                    }
                }
                .comment-reply {
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    &:hover {
                        color: #333;
                    }
                    .iconfont {
                        font-size: 16px;
                        margin-right: 5px;
                    }
                }

            }
            .reply {
                margin: 10px 0;
                border-left: 2px solid #DCDFE6;
                .item {
                    margin: 0 10px;
                    padding: 10px 0;
                    border-bottom: 1px dashed #EBEEF5;
                    .reply-content {
                        display: flex;
                        align-items: center;
                        font-size: 14px;
                        color: #303133;
                        .from-name {
                            color: #409EFF;
                        }
                        .to-name {
                            color: #409EFF;
                            margin-left: 5px;
                            margin-right: 5px;
                        }
                    }
                    .reply-bottom {
                        display: flex;
                        align-items: center;
                        margin-top: 6px;
                        font-size: 12px;
                        color: #909399; //次要文字
                        .reply-text {
                            display: flex;
                            align-items: center;
                            margin-left: 10px;
                            cursor: pointer;
                            &:hover {
                                color: #333;
                            }
                            .icon-comment {
                                margin-right: 5px;
                            }
                        }
                    }
                }
                .write-reply {
                    display: flex;
                    align-items: center;
                    font-size: 14px;
                    color: #909399; //次要文字
                    padding: 10px;
                    cursor: pointer;
                    &:hover {
                        color: #303133;
                    }
                    .el-icon-edit {
                        margin-right: 5px;
                    }
                }
                .fade-enter-active, fade-leave-active {
                    transition: opacity 0.5s;
                }
                .fade-enter, .fade-leave-to {
                    opacity: 0;
                }
                .input-wrapper {
                    padding: 10px;
                    .gray-bg-input, .el-input__inner {
                        /*background-color: #67C23A;*/
                    }
                    .btn-control {
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        padding-top: 10px;
                        .cancel {
                            font-size: 16px;
                            color: #606266;
                            margin-right: 20px;
                            cursor: pointer;
                            &:hover {
                                color: #333;
                            }
                        }
                        .confirm {
                            font-size: 16px;
                        }
                    }
                }
            }
        }
    }
  .comment-right{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    padding: 10px;
    border-bottom: 1px solid #f2f6fc;

  }
    .container .comment-right .info{
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: row-reverse;
      flex-direction: row-reverse;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
    }
    .container .comment-right{
      text-align:right;
    }
</style>
